.intro {
  margin-bottom: 22px;
  padding-top: 26px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-image: url("../../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;

  .intro__title {
    margin: 0;
    margin-bottom: 26px;
    padding: 0;
    font-family: "Oswald Regular", "Arial", sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 36px;
    text-align: center;
    color: #ffffff;
  }

  .intro__slogan {
    margin: 0 auto;
    margin-bottom: 28px;
    width: 280px;
    font-family: "Oswald Regular", "Arial", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: #ffffff;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    word-wrap: break-word;
  }

  .intro__profuct-image {
    margin: 0;
    margin-bottom: 4px;
    padding: 0;
  }
}
